    </CContainer>
  </div>
</template>

<script>
import axios from "axios";
// import User from '../../models/user'

export default {
  name: 'Login',
  components: {},
  data() {
    return {
      email: '',
      password: '',
      logins: []
    }
  },
  methods: {
    handleLogin(data){
      this.login = data;

      // this.token = localStorage.setItem('token', api_token);
      // this.userId = localStorage.setItem('adminUserId', id);

      const response = 
        axios
          .post('http://103.151.215.74/api/admin/login', {
            email: this.email,
            password: this.password
          })
          .then(
            // (response) => this.handleSubmit(response.data.json_data)
             (response) => {
              // handle error
              // console.log("Berhasil : ", response);
              localStorage.setItem('token', response.data.json_data.api_token);
              localStorage.setItem('admin_id', response.data.json_data.id);
              this.$router.push('/menus/forum');
            }
          )
          .catch(function (error) {
            // handle error
            // console.log("Gagal : ", error);
          });
        
    }
  },
}

</script>
